<template>
  <div>
    <cms-breadcrumb to="/messageSet/systemTpl/list" @click="rest">
      <li>系统消息模板-修改</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li> 设置系统发送消息的内容、方式</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="模板标识" prop="mesCode" :error='errorMessage.mesCode'>
          <div class="message-flow">{{formData.mesCode}}</div>
        </el-form-item>
        <el-form-item label="模板标题" prop="mesTitle" :error='errorMessage.mesTitle'>
          <el-input v-model="formData.mesTitle" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="模板描述" prop="remark" :error='errorMessage.remark'>
          <el-input v-model="formData.remark" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="details[0].isOpen">
          <cms-switch v-model="formData.details[0].isOpen"  @change="changeOpen"></cms-switch>
        </el-form-item>
        <el-form-item label="模板内容" prop="details[0].mesContent" ref="mesContent"
          :rules="formData.details[0].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
          <el-input v-model="formData.details[0].mesContent" type="textarea" :rows="4" maxlength="200"></el-input>
          <span class="gray message-flow text-overflow" v-if="!isEdit[0]">{{formData.details[0].extendedField}} 
            <i class="el-icon-edit message-icon" title="修改" @click="setEdit(0)"></i>
          </span>
        </el-form-item>
        <el-form-item label="提示文案" prop="details[0].extendedField" v-if="isEdit[0]">
          <el-input v-model="formData.details[0].extendedField" maxlength="200"></el-input>
          <el-button size="mini" type="primary" @click="setCancel(0)">取消</el-button>
        </el-form-item>
        </el-form>
        <div class="cms-form-footer">
          <cms-button @click="update(true)" v-color>保存</cms-button>
          <cms-button type='rest' @click="reset">重置</cms-button>
        </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { messageTplGet, messageTpUpdate } from '@/api/setting/messageSet.js'
var defaultData = {
  id: '',
  mesCode: '',
  mesTitle: '',
  remark: '',
  mesType: '',
  tempData: '',
  details: [
    {
      id: '',
      isOpen: '',
      mesType: '4',
      mesContent: ''
    }
  ]
}
export default {
  name: 'messageSetShopEdit',
  mixins: [form],
  data() {
    return {
      activeName: '1',
      formData: Object.assign({}, defaultData),
      isEdit: [false],
      tempData: '', // 暂存提示文案
      saveRules: {
        // 新增规则
        mesCode: [
          rules.required('请填写模板标识'),
          rules.english(),
          rules.len(1, 150)
        ],
        mesTitle: [rules.required('请填写模板标题'), rules.len(1, 150)],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    setEdit(index) {
      this.tempData = this.formData.details[index].extendedField
      this.$set(this.isEdit, index, true)
    },
    setCancel(index) {
      this.$set(this.formData.details[index], 'extendedField', this.tempData)
      this.$set(this.isEdit, index, false)
    },
    getFormData(id) {
      // 通过id获取表单信息
      messageTplGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    changeOpen(status) {
      if (!status) {
        this.$refs.mesContent.clearValidate()
      }
    },
    update(state) {
      this.formData.mesType = this.$route.query.mesType
      this.updateForm(
        messageTpUpdate,
        this.formData,
        '/messageSet/systemTpl/list',
        'list'
      )
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.clearValidate()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>

<style scoped>
.message-flow{
  min-height: 18px;
  max-width: 380px;
  word-break: break-all;
  padding-right: 10px;
}
</style>



